@use 'sass:math';
@use 'base/variables' as v;
@use 'base/mixins' as m;

.cards{
    margin: v.$separate 0;
    &__container{
        display: flex;
        flex-wrap: wrap;
        gap: 2rem;
        align-items: center;
    }
    &__card{
        @include m.card;
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-bottom: 2rem;
        padding: 2rem 3rem 2rem 2rem;
        border-radius: .5rem;
        @include m.tablet{
            width: auto;
        }
    }
    &__info{
        margin-bottom: 1rem;
        &--div{
            display: flex;
            flex-wrap: wrap;
            gap: 0.5rem;        }
    }
    &__img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: .5rem;
        max-width: 25rem;
    }
    &__label, &__text{
        color: v.$black;
        margin: 0;
        &--span{
            font-weight: v.$bold;
        }
    }
    &__label{
        font-weight: v.$bold;
    }
    &__actions{
        display: flex;
        justify-content: space-between;
        gap: 1rem;
        margin-top: auto;
    }
}